"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Progress } from "@/components/ui/progress";
import Link from "next/link";
import { useParams } from "next/navigation";

export default function CampaignDetailPage() {
  const params = useParams();
  const campaignId = params.id as string;

  // Sample campaign data - in a real app, this would be fetched based on the ID
  const campaign = {
    id: campaignId,
    name: "Annual Fund 2024",
    description: "Supporting student scholarships and academic excellence at Charleston Law School",
    targetGoal: 500000,
    raisedAmount: 325000,
    startDate: "2024-01-01",
    endDate: "2024-12-31",
    status: "Active",
    donorCount: 245,
    category: "Annual Fund",
    averageGift: 1327,
    lastUpdated: "2024-01-20",
  };

  // Sample donation data
  const recentDonations = [
    {
      id: "1",
      donorName: "Sarah Johnson",
      amount: 5000,
      date: "2024-01-15",
      method: "Credit Card",
      designation: "Student Scholarships",
    },
    {
      id: "2",
      donorName: "Charleston Legal Foundation",
      amount: 50000,
      date: "2024-01-10",
      method: "Bank Transfer",
      designation: "General Fund",
    },
    {
      id: "3",
      donorName: "Michael Chen",
      amount: 10000,
      date: "2024-01-08",
      method: "Check",
      designation: "Student Scholarships",
    },
    {
      id: "4",
      donorName: "Roberts & Associates",
      amount: 15000,
      date: "2024-01-05",
      method: "Bank Transfer",
      designation: "Faculty Support",
    },
    {
      id: "5",
      donorName: "Lisa Williams",
      amount: 3500,
      date: "2024-01-03",
      method: "Credit Card",
      designation: "Student Scholarships",
    },
  ];

  // Sample campaign activities
  const campaignActivities = [
    {
      id: "1",
      date: "2024-01-20",
      activity: "Email Campaign Sent",
      description: "Sent personalized solicitation emails to 500 prospects",
      staff: "Jennifer Adams",
    },
    {
      id: "2",
      date: "2024-01-15",
      activity: "Major Gift Received",
      description: "Received $50,000 gift from Charleston Legal Foundation",
      staff: "Michael Roberts",
    },
    {
      id: "3",
      date: "2024-01-10",
      activity: "Phone Campaign",
      description: "Completed phone calls to 50 major donor prospects",
      staff: "Jennifer Adams",
    },
    {
      id: "4",
      date: "2024-01-05",
      activity: "Direct Mail Sent",
      description: "Mailed 1,000 solicitation letters to alumni",
      staff: "Sarah Mitchell",
    },
  ];

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("en-US", {
      style: "currency",
      currency: "USD",
    }).format(amount);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString("en-US", {
      year: "numeric",
      month: "long",
      day: "numeric",
    });
  };

  const getProgressPercentage = () => {
    return Math.min((campaign.raisedAmount / campaign.targetGoal) * 100, 100);
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case "Active":
        return "bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300";
      case "Completed":
        return "bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-300";
      case "Planned":
        return "bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-300";
      case "Paused":
        return "bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-300";
      default:
        return "bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-300";
    }
  };

  const getDaysRemaining = () => {
    const today = new Date();
    const end = new Date(campaign.endDate);
    const diffTime = end.getTime() - today.getTime();
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    return diffDays;
  };

  const daysRemaining = getDaysRemaining();

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 dark:from-slate-900 dark:to-slate-800">
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <div>
            <h1 className="text-3xl font-bold text-slate-900 dark:text-slate-100 mb-2">
              Campaign Details
            </h1>
            <p className="text-slate-600 dark:text-slate-400">
              Comprehensive campaign management and tracking
            </p>
          </div>
          <div className="flex gap-3">
            <Link href="/institutional-advancement/campaigns">
              <Button variant="outline" className="border-slate-300 dark:border-slate-600">
                Back to Campaigns
              </Button>
            </Link>
            <Button className="bg-slate-900 hover:bg-slate-800 dark:bg-slate-100 dark:hover:bg-slate-200 dark:text-slate-900">
              Edit Campaign
            </Button>
          </div>
        </div>

        {/* Campaign Overview */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 mb-8">
          {/* Main Info Card */}
          <Card className="lg:col-span-2 border-slate-200 dark:border-slate-700">
            <CardHeader>
              <div className="flex items-start justify-between mb-4">
                <div>
                  <CardTitle className="text-2xl text-slate-900 dark:text-slate-100 mb-2">
                    {campaign.name}
                  </CardTitle>
                  <div className="flex gap-2 mb-3">
                    <Badge variant="outline">{campaign.category}</Badge>
                    <Badge className={getStatusColor(campaign.status)}>
                      {campaign.status}
                    </Badge>
                    {daysRemaining > 0 && (
                      <Badge variant="outline">
                        {daysRemaining} days remaining
                      </Badge>
                    )}
                  </div>
                </div>
              </div>
              <p className="text-slate-600 dark:text-slate-400 mb-4">
                {campaign.description}
              </p>
            </CardHeader>
            <CardContent>
              {/* Progress Section */}
              <div className="space-y-4 mb-6">
                <div className="flex justify-between items-center">
                  <span className="text-sm text-slate-600 dark:text-slate-400">Campaign Progress</span>
                  <span className="text-sm font-medium text-slate-900 dark:text-slate-100">
                    {getProgressPercentage().toFixed(1)}%
                  </span>
                </div>
                <Progress value={getProgressPercentage()} className="h-3" />
                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>
                    <span className="text-slate-600 dark:text-slate-400">Raised:</span>
                    <span className="ml-2 font-semibold text-slate-900 dark:text-slate-100">
                      {formatCurrency(campaign.raisedAmount)}
                    </span>
                  </div>
                  <div>
                    <span className="text-slate-600 dark:text-slate-400">Goal:</span>
                    <span className="ml-2 font-semibold text-slate-900 dark:text-slate-100">
                      {formatCurrency(campaign.targetGoal)}
                    </span>
                  </div>
                </div>
              </div>

              {/* Campaign Details */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <h4 className="font-semibold text-slate-900 dark:text-slate-100 mb-3">Campaign Information</h4>
                  <div className="space-y-2 text-sm">
                    <div>
                      <span className="text-slate-600 dark:text-slate-400">Start Date:</span>
                      <span className="ml-2 text-slate-900 dark:text-slate-100">{formatDate(campaign.startDate)}</span>
                    </div>
                    <div>
                      <span className="text-slate-600 dark:text-slate-400">End Date:</span>
                      <span className="ml-2 text-slate-900 dark:text-slate-100">{formatDate(campaign.endDate)}</span>
                    </div>
                    <div>
                      <span className="text-slate-600 dark:text-slate-400">Last Updated:</span>
                      <span className="ml-2 text-slate-900 dark:text-slate-100">{formatDate(campaign.lastUpdated)}</span>
                    </div>
                  </div>
                </div>
                <div>
                  <h4 className="font-semibold text-slate-900 dark:text-slate-100 mb-3">Performance Metrics</h4>
                  <div className="space-y-2 text-sm">
                    <div>
                      <span className="text-slate-600 dark:text-slate-400">Total Donors:</span>
                      <span className="ml-2 text-slate-900 dark:text-slate-100">{campaign.donorCount.toLocaleString()}</span>
                    </div>
                    <div>
                      <span className="text-slate-600 dark:text-slate-400">Average Gift:</span>
                      <span className="ml-2 text-slate-900 dark:text-slate-100">{formatCurrency(campaign.averageGift)}</span>
                    </div>
                    <div>
                      <span className="text-slate-600 dark:text-slate-400">Remaining to Goal:</span>
                      <span className="ml-2 text-slate-900 dark:text-slate-100">
                        {formatCurrency(campaign.targetGoal - campaign.raisedAmount)}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Quick Stats Card */}
          <Card className="border-slate-200 dark:border-slate-700">
            <CardHeader>
              <CardTitle className="text-lg text-slate-900 dark:text-slate-100">
                Quick Actions
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <Button className="w-full bg-slate-900 hover:bg-slate-800 dark:bg-slate-100 dark:hover:bg-slate-200 dark:text-slate-900">
                Add Donation
              </Button>
              <Button variant="outline" className="w-full border-slate-300 dark:border-slate-600">
                Send Email Campaign
              </Button>
              <Button variant="outline" className="w-full border-slate-300 dark:border-slate-600">
                Generate Report
              </Button>
              <Button variant="outline" className="w-full border-slate-300 dark:border-slate-600">
                Export Data
              </Button>
            </CardContent>
          </Card>
        </div>

        {/* Detailed Information Tabs */}
        <Tabs defaultValue="donations" className="space-y-6">
          <TabsList className="grid w-full grid-cols-2 lg:w-auto lg:grid-cols-2">
            <TabsTrigger value="donations">Recent Donations</TabsTrigger>
            <TabsTrigger value="activities">Campaign Activities</TabsTrigger>
          </TabsList>

          <TabsContent value="donations">
            <Card className="border-slate-200 dark:border-slate-700">
              <CardHeader>
                <CardTitle className="text-lg text-slate-900 dark:text-slate-100">
                  Recent Donations
                </CardTitle>
                <CardDescription className="text-slate-600 dark:text-slate-400">
                  Latest donations received for this campaign
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Donor</TableHead>
                      <TableHead>Amount</TableHead>
                      <TableHead>Date</TableHead>
                      <TableHead>Method</TableHead>
                      <TableHead>Designation</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {recentDonations.map((donation) => (
                      <TableRow key={donation.id}>
                        <TableCell className="font-medium">{donation.donorName}</TableCell>
                        <TableCell className="font-semibold">
                          {formatCurrency(donation.amount)}
                        </TableCell>
                        <TableCell>{formatDate(donation.date)}</TableCell>
                        <TableCell>{donation.method}</TableCell>
                        <TableCell>{donation.designation}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="activities">
            <Card className="border-slate-200 dark:border-slate-700">
              <CardHeader>
                <CardTitle className="text-lg text-slate-900 dark:text-slate-100">
                  Campaign Activities
                </CardTitle>
                <CardDescription className="text-slate-600 dark:text-slate-400">
                  Recent activities and milestones for this campaign
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {campaignActivities.map((activity) => (
                    <div key={activity.id} className="border-l-4 border-slate-200 dark:border-slate-700 pl-4 py-2">
                      <div className="flex items-center justify-between mb-2">
                        <div className="flex items-center gap-2">
                          <span className="text-sm font-medium text-slate-900 dark:text-slate-100">
                            {activity.activity}
                          </span>
                        </div>
                        <span className="text-sm text-slate-500 dark:text-slate-500">
                          {formatDate(activity.date)}
                        </span>
                      </div>
                      <p className="text-sm text-slate-600 dark:text-slate-400 mb-1">
                        {activity.description}
                      </p>
                      <p className="text-xs text-slate-500 dark:text-slate-500">
                        Staff: {activity.staff}
                      </p>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
